.header {
  .header-banner {
    position: relative;
    width: 100%;
    height: 17.3125rem;
    background-repeat: no-repeat;
    background-position: right center;
    background-size: cover;
    @include media-breakpoint-up(md) {
      height: 31rem;
    }
  }
  .header-banner-sm {
    height: 32.375rem;
    background-position: center top;
    @include media-breakpoint-up(md) {
      height: 28.75rem;
      background-position: center center;
    }
  }

  .header-slogan {
    position: absolute;
    top: 1.875rem;
    right: 1.875rem;
    width: 3.75rem;

    @include media-breakpoint-up(md) {
      top: 2.5rem;
      right: 2.5rem;
      width: 5.625rem;
    }
  }

  // 高度
  .header-menu-img > *,
  .header-menu > * {
    height: 11.25rem;
    @include media-breakpoint-up(md) {
      height: 16rem;
    }
    @include media-breakpoint-up(lg) {
      height: 19rem;
    }
  }

  // 位移
  .header-menu {
    margin-top: -11.25rem;
    @include media-breakpoint-up(md) {
      margin-top: -16rem;
    }
    @include media-breakpoint-up(lg) {
      margin-top: -19rem;
    }
  }

  .header-menu-img > * {
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    transition: transform .4s;
  }

  .header-menu > * {
    color: $white;
    letter-spacing: .25rem;
    cursor: pointer;
    background-color: rgba($primary, .8);
    backdrop-filter: blur(5px);
    transition: background-color .4s;
    &:hover {
      background-color: rgba($primary, .7);
    }

    &:first-child {
      color: $primary;
      background-color: rgba($warning, .8);
      &:hover {
        background-color: rgba($warning, .7);
      }
    }
  }

  .header-status {
    position: absolute;
    top: 13rem;
    left: 50%;
    width: 100%;
    transform: translateX(-50%);
    @include media-breakpoint-up(md) {
      top: 8rem;
      width: 18.75rem;
      transform: translateX(-50%);
    }
  }
}
